import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex,axios)

export default new Vuex.Store({
  state: {
    products: [],
    shoppingcar: [],
    shopcar: false,
  },
  mutations: {
    add(state,data){
      data.forEach(function(item){
        
        item.count = 1;
      });
      console.log(data)
      state.products = data
    },
    addshop(state,data){
      var findLike = state.shoppingcar.find(item => item.name == data.name);
      if(!findLike){
        state.shoppingcar.push(data)
      console.log(state.shoppingcar)
      }
    },
    togglecar(state){
      state.shopcar = !state.shopcar
    },
    closecar(state){
      state.shopcar = false
    },
    additemcount(state,data){
      state.shoppingcar.forEach(function(item){
        if(data  == item.name){
          console.log('OK')
          item.count = item.count + 1
        }
      
      })
      console.log(state.products)
    },
    delitemcount(state,data){
      //let origin = state.products
      state.shoppingcar.forEach(function(item){
        if(data  == item.name & item.count > 0){
          console.log('OK')
          item.count = item.count - 1
        }
      
      })
     
    },
  },
  actions: {
    Getproduct({ commit }){
      const url = 'https://my-json-server.typicode.com/jubs16/Products/Products';
      axios.get(url).then((response) => {
      console.log(response.data)
      return response.data})
      .then(function(data){
        commit('add',data)
      })
    },
    Addcarlist({commit}, payload){
      commit('addshop',payload)
    },
    Togglecar({commit}){
      commit('togglecar')
    },
    Closecar({commit}){
      commit('closecar')
    },
    Additemcount({commit},payload){
      console.log(payload)
      commit('additemcount',payload)
    },
    Delitemcount({commit},payload){
      commit('delitemcount',payload)
    }
    
  },
  getters:{
    throwdata: state => state.products,
    throwcar: state => state.shopcar,
    shoppingcar: state => state.shoppingcar
  },
  
})
