<template>
<div class='container'>
  <div v-for="(item, index) in throwdata" :key="item.name">
    <div class="card" @click="addtocar(item)" v-if="index < num & index !=2">
      <img :src="item.imgUrl"  class="card-img-top">
      <div class="card-body">
        <h5 class="card-title">{{item.name}}</h5>
        <p class="card-text">${{item.price}}</p>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: 'Product',
  props:['num',],
  methods:{
    test(){
      console.log(this.$store)
    },
    addtocar(item){
       this.$store.dispatch('Addcarlist',item)
    }
  },
  created(){
    this.$store.dispatch('Getproduct')
  },
  computed: {
  ...mapGetters(["throwdata"])
},

}
</script>

<style lang="scss" scoped>
  .container{
    width: 80vw;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    background-color: rgb(207, 207, 207);
  }
  .card{
    width: 20vw;
    height: 20vw;
    min-width: 300px;
    min-height: 300px;
    margin: 2rem 0 2rem 0;
    transition: 1s ease;
  }
  .card:hover{
    opacity: .7;
  }
  .card-img-top{
    width: 90%;
    height: 70%;
    margin: auto;
  }
  
</style>